.App {
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 100vh; 
}

header {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 2rem;
}

h1{
  text-transform: uppercase;
  text-align: center;
  color: #888;
  font-size: 42px;
  margin-bottom: 1rem;
}

.searchBox{
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 1rem;
  overflow: hidden;
  width: 100%;
  max-width: 480px;
  margin-bottom: 1rem;
  transition: 0.6s;
}

.searchBox input {
  flex: 3;
  display: block;
  border: 0;
  outline: 0;
  appearance: none;
  width: 100%;
  padding: 1rem;
  transition: 0.4s;
}

#searchBtn{
  flex: 1;
  background: green;
  color: #fff;
  font-weight: bold;
  text-transform: uppercase;
  border-radius: 0 1rem 1rem 0;
  cursor: pointer;
}

#searchBtn:hover{
  background: white;
  color: green;
  border: 1px solid green;
}

.searchBox:focus-within {
  box-shadow: 3px 3px 6px rgba(0,0,0,0.2);
  transform: translateY(-5px);
}

.results{
  max-width: 700px;
  margin: 0 auto;
}

.result {
  width: 100%;
  padding: 1rem;
  margin-bottom: 1rem;
  background: #fff;
  border-radius: 1rem;
  transition: 0.4s;
}

.result:hover{
  box-shadow: 3px 3px 6px rgba(0,0,0,0.2);
}

.result h3{
  color: #888;
  font-size: 1.7rem;
  margin-bottom: 1rem;
}

.result p{
  color: #313131;
  font-size: 1.1rem;
  margin-bottom: 1rem;
}

/* Styling for search phrase (or word) */
.result p .searchmatch {
  font-weight: 700;
  color: green;
}

.result a{
  display: inline-block;
  padding: 0.8rem 1rem;
  background: green;
  color: #fff;
  font-size: 0.8rem;
  text-decoration: none;
  text-transform: capitalize;
  border-radius: 0.8rem;
  font-weight: 700;
  transition: 0.4s;
  cursor: pointer;
}

.result a:hover{
  background: white;
  color: green;
  border: 1px solid green;
}

@media only screen and (max-width: 480px){
  .searchBox{
    display: flex;
    flex-direction: column;
  }

  #searchBtn{
    border-radius: 0;
  }
}